import { loadSearch, loadComment, loadWatchedMovie, loadWantedMovie, loadCelebrity } from '@/common/js/cache';
const state = {
  movie: {}, // 
  currentCelebrityId: 0,
  currentReviewId: 0,
  currentDiscussionType: '',
  searchHistory: loadSearch(), // ,
  watchedMovies: loadWatchedMovie(), // 
  wantedMovies: loadWantedMovie(), // 
  collectedCelebrities: loadCelebrity(), // 
  favoriteComments: loadComment() // 
};

export default state;



